.Pokecard {
    border: 2px solid black;
    width: 50vh;
    padding: 10px;
    margin: 1rem;
    box-shadow: 7px 10px 12px -5px rgba(0,0,0,0.56)
}
.Pokecard-data {
    font-size: 80%;
    text-transform: uppercase;
}

.Pokecard-image {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 250px;
    background-color: #eceff1;
    border-radius: 3px;
    margin-bottom: 1rem;
}